<template>
  <v-card class="mx-auto mb-4">
    <v-list-item>
      <v-list-item-content>
        <v-list-item-title class="headline">{{
          article.title
        }}</v-list-item-title>
        <v-list-item-subtitle>{{
          this.getHostname() + " | " + this.getDateTime()
        }}</v-list-item-subtitle>
      </v-list-item-content>
    </v-list-item>

    <v-card-text>
      <p class="content" v-html="article.content"></p>
    </v-card-text>

    <v-card-actions>
      <v-btn :href="article.link" target="_blank" text color="blue accent-4">
        Lire
        <v-icon> mdi-arrow-right-bold-circle-outline </v-icon>
      </v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
const parseDate = tdate => {
  const systemDate = new Date(Date.parse(tdate));
  const userDate = new Date();
  const diff = Math.floor((userDate - systemDate) / 1000);
  if (diff < 59) {
    return diff + "s";
  }
  if (diff <= 3540) {
    return Math.round(diff / 60) + " m";
  }
  if (diff <= 86400) {
    return Math.round(diff / 3600) + " h";
  }
  if (diff < 604800) {
    return Math.round(diff / 86400) + " j";
  }
  return systemDate.toString().substring(4, 10);
};

export default {
  name: "Article",
  props: ["article"],
  methods: {
    getHostname() {
      try {
        const urlObj = new URL(this.article.link);
        return urlObj.hostname.replace("www.", "").replace("ww2.", "");
      } catch (e) {
        console.error(e.toString());
      }
    },
    getDateTime() {
      if (this.article.isoDate) {
        return parseDate(this.article.isoDate);
      }
    }
  }
};
</script>

<style scoped>
.body .content {
  margin-top: 5px;
}
.content {
  text-align: justify;
}
.middot {
  font-size: 12px;
  margin: 0 8px;
}
a {
  color: #42b983;
}
</style>
