<template>
  <v-form
    class="form"
    ref="form"
    @submit.prevent="submit"
    v-model="valid"
    lazy-validation
  >
    <v-snackbar v-model="snackbar" :color="color" :top="top" :timeout="timeout">
      Identifiants incorrect !
      <v-btn color="black" text @click="snackbar = false">
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </v-snackbar>
    <v-text-field
      v-model="email"
      :rules="emailRules"
      label="E-mail"
      required
      outlined=""
    ></v-text-field>
    <v-text-field
      :append-icon="show4 ? 'mdi-eye' : 'mdi-eye-off'"
      :passwordRules="[passwordRules.required, passwordRules.nameMatch]"
      :type="show4 ? 'text' : 'password'"
      v-model="password"
      :rules="passwordRules"
      label="Mot de passe"
      required
      outlined=""
      @click:append="show4 = !show4"
    ></v-text-field>

    <v-btn :disabled="!valid" color="success" class="ml-5" @click="submit()">
      Validate
    </v-btn>

    <v-btn color="error" class="ml-5" @click="reset">
      Reset Form
    </v-btn>
  </v-form>
</template>

<script>
import firebase from "firebase";
export default {
  data() {
    return {
      snackbar: false,
      valid: true,
      color: "red",
      top: true,
      timeout: 2000,
      email: "",
      password: "",
      show4: false,
      emailRules: [
        v => !!v || "Veuillez renseigner votre E-mail.",
        v => /.+@.+\..+/.test(v) || "Votre E-mail n'est pas valide."
      ],
      passwordRules: [v => !!v || "Veuillez renseigner votre mot de passe."],
      error: null
    };
  },
  methods: {
    submit() {
      if (this.$refs.form.validate()) {
        firebase
          .auth()
          .signInWithEmailAndPassword(this.email, this.password)
          // eslint-disable-next-line no-unused-vars
          .then(data => {
            this.$router.replace({ name: "addCar" });
          })
          .catch(err => {
            if ((this.error = err.message)) {
              this.snackbar = true;
            }
          });
      }
    },
    reset() {
      this.$refs.form.reset();
    }
  }
};
</script>
<style>
.form {
  margin: 10px;
  margin-top: 35px;
}
</style>
