<template>
  <v-container id="listCharacteristics">
    <v-row
      v-for="characteristic in characteristics"
      :key="characteristic.id"
      :characteristic="characteristic"
      cols="12"
    >
      <v-col v-if="characteristic.id_trim == TrimURLId" cols="12">
        <v-card dark>
          <div>
            <v-list disabled="disabled">
              <v-subheader>Model : {{ characteristic.name }}</v-subheader>
              <v-list-item-group>
                <v-list-item>
                  <v-list-item-icon class="icon">
                    <v-icon>mdi-speedometer</v-icon>
                  </v-list-item-icon>
                  <v-list-item-content>
                    <v-list-item-title
                      v-if="characteristic.acceleration"
                      v-text="characteristic.acceleration + ' s'"
                    ></v-list-item-title>
                    <v-list-item-title v-else>-</v-list-item-title>
                  </v-list-item-content>
                  <v-list-item-icon class="icon">
                    <v-icon>mdi-flash-circle</v-icon>
                  </v-list-item-icon>
                  <v-list-item-content>
                    <v-list-item-title
                      v-if="characteristic.consommation"
                      v-text="characteristic.consommation + ' kWh'"
                    ></v-list-item-title>
                    <v-list-item-title
                      v-if="characteristic.consommation"
                      v-text="characteristic.norm"
                    ></v-list-item-title>
                    <v-list-item-title v-else>-</v-list-item-title>
                  </v-list-item-content>
                  <v-list-item-icon class="icon hidden-xs-only">
                    <v-icon>mdi-dumbbell</v-icon>
                  </v-list-item-icon>
                  <v-list-item-content class="hidden-xs-only">
                    <v-list-item-title
                      v-if="characteristic.torque"
                      v-text="characteristic.torque + ' Nm'"
                    ></v-list-item-title>
                    <v-list-item-title v-else>-</v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
                <v-list-item>
                  <v-list-item-icon class="icon">
                    <v-icon>mdi-battery-70</v-icon>
                  </v-list-item-icon>
                  <v-list-item-content>
                    <v-list-item-title
                      v-if="characteristic.autonomy"
                      v-text="characteristic.autonomy + ' kms'"
                    ></v-list-item-title>
                    <v-list-item-title
                      v-if="characteristic.autonomy"
                      v-text="characteristic.norm"
                    ></v-list-item-title>
                    <v-list-item-title v-else>-</v-list-item-title>
                  </v-list-item-content>
                  <v-list-item-icon class="icon">
                    <v-icon>mdi-battery-unknown</v-icon>
                  </v-list-item-icon>
                  <v-list-item-content>
                    <v-list-item-title
                      v-if="characteristic.battery_capacity"
                      v-text="characteristic.battery_capacity + ' kWh'"
                    ></v-list-item-title>
                    <v-list-item-title v-else>-</v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
                <v-list-item>
                  <v-list-item-icon class="icon">
                    <v-icon>AC</v-icon>
                  </v-list-item-icon>
                  <v-list-item-content>
                    <v-list-item-title
                      v-if="
                        characteristic.max_power_AC &&
                          characteristic.connector_AC
                      "
                      v-text="characteristic.max_power_AC"
                    ></v-list-item-title>
                    <v-list-item-title
                      v-if="characteristic.max_power_AC"
                      v-text="characteristic.connector_AC"
                    ></v-list-item-title>
                    <v-list-item-title v-else>-</v-list-item-title>
                  </v-list-item-content>
                  <v-list-item-icon class="icon">
                    <v-icon>DC</v-icon>
                  </v-list-item-icon>
                  <v-list-item-content>
                    <v-list-item-title
                      v-if="
                        characteristic.max_power_DC &&
                          characteristic.connector_DC
                      "
                      v-text="characteristic.max_power_DC"
                    ></v-list-item-title>
                    <v-list-item-title
                      v-if="characteristic.max_power_DC"
                      v-text="characteristic.connector_DC"
                    ></v-list-item-title>
                    <v-list-item-title v-else>-</v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
                <v-list-item>
                  <v-list-item-icon class="icon">
                    <v-icon>mdi-seat</v-icon>
                  </v-list-item-icon>
                  <v-list-item-content>
                    <v-list-item-title
                      v-if="characteristic.seats"
                      v-text="characteristic.seats"
                    ></v-list-item-title>
                    <v-list-item-title v-else>-</v-list-item-title>
                  </v-list-item-content>
                  <v-list-item-icon class="icon">
                    <v-icon>mdi-weight-kilogram</v-icon>
                  </v-list-item-icon>
                  <v-list-item-content>
                    <v-list-item-title
                      v-if="characteristic.weight"
                      v-text="characteristic.weight + ' kgs'"
                    ></v-list-item-title>
                    <v-list-item-title v-else>-</v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
                <v-list-item>
                  <v-list-item-icon class="icon">
                    <v-icon>mdi-car-back</v-icon>
                  </v-list-item-icon>
                  <v-list-item-content>
                    <v-list-item-title
                      v-if="characteristic.boot_min && characteristic.boot_max"
                      v-text="
                        characteristic.boot_min +
                          ' L | ' +
                          characteristic.boot_max +
                          ' L'
                      "
                    ></v-list-item-title>
                    <v-list-item-title
                      v-else-if="characteristic.boot_min"
                      v-text="characteristic.boot_min + ' L | -'"
                    ></v-list-item-title>
                    <v-list-item-title v-else>- | -</v-list-item-title>
                  </v-list-item-content>
                  <v-list-item-icon class="icon">
                    <v-icon>mdi-ruler</v-icon>
                  </v-list-item-icon>
                  <v-list-item-content>
                    <v-list-item-title
                      v-if="characteristic.length"
                      v-text="
                        '' +
                          characteristic.length +
                          'x' +
                          characteristic.width +
                          'x' +
                          characteristic.height
                      "
                    ></v-list-item-title>
                    <v-list-item-title v-else>-</v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
              </v-list-item-group>
            </v-list>
          </div>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  props: {
    characteristic: []
  },
  data() {
    return {
      characteristics: [],
      TrimURLId: this.$route.params.idTrim,
      item: ""
    };
  },
  created() {
    this.$axios.get("characteristic.json").then(response => {
      this.characteristics = response.data;
    });
  }
};
</script>

<style type="text/css">
.icon {
  margin-right: 7px !important;
}
</style>
