import Vue from "vue";
import VueRouter from "vue-router";
import News from "../views/News.vue";
import Cars from "../views/Cars.vue";
import ListModels from "../components/ListModels.vue";
import ListGenerations from "../components/ListGenerations.vue";
import ListTrims from "../components/ListTrims.vue";
import ListCharacteristics from "../components/ListCharacteristics.vue";
import Login from "../components/Admin/Login.vue";
import AddCar from "../components/Admin/AddCar.vue";
import UpdateCar from "../components/Admin/UpdateCar.vue";
import DeleteCar from "../components/Admin/DeleteCar.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/news",
    name: "news",
    component: News
  },
  {
    path: "/cars",
    name: "cars",
    component: Cars
  },
  {
    path: "/cars/:idBrand/",
    name: "models",
    component: ListModels
  },
  {
    path: "/cars/:idBrand/:idModel",
    name: "generations",
    component: ListGenerations
  },
  {
    path: "/cars/:idBrand/:idModel/:idGeneration",
    name: "trims",
    component: ListTrims
  },
  {
    path: "/cars/:idBrand/:idModel/:idGeneration/:idTrim",
    name: "characteristics",
    component: ListCharacteristics
  },
  {
    path: "/admin/login",
    name: "login",
    component: Login
  },
  {
    path: "/admin/cars/add",
    name: "addCar",
    component: AddCar
  },
  {
    path: "/admin/cars/update",
    name: "updateCar",
    component: UpdateCar
  },
  {
    path: "/admin/cars/delete",
    name: "deleteCar",
    component: DeleteCar
  },
  {
    path: "**",
    name: "default",
    component: Cars
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
