<template>
  <div v-if="user" class="alert alert-success" role="alert">
    <div class="card-header">Ajouter une nouvelle voiture</div>
    <v-row align="center" justify="center">
      <v-btn-toggle rounded dense="">
        <v-btn color="green" to="/admin/cars/add">
          <v-icon right>mdi-plus-one</v-icon>
        </v-btn>
        <v-btn color="blue" to="/admin/cars/update">
          <v-icon right>mdi-refresh</v-icon>
        </v-btn>
        <v-btn color="red" to="/admin/cars/delete">
          <v-icon right>mdi-delete</v-icon>
        </v-btn>
      </v-btn-toggle>
    </v-row>
    <v-expansion-panels inset="">
      <v-expansion-panel>
        <v-expansion-panel-header>
          <template v-slot:default="{ open }">
            <v-row no-gutters>
              <v-col cols="4">Marque</v-col>
              <v-col cols="8" class="text--secondary">
                <v-fade-transition leave-absolute>
                  <span v-if="open" key="0">
                    Renseignez la marque
                  </span>
                  <span v-else key="1">
                    {{ car.brand }}
                  </span>
                </v-fade-transition>
              </v-col>
            </v-row>
          </template>
        </v-expansion-panel-header>
        <v-expansion-panel-content>
          <v-select
            v-model="car.brand"
            :items="brands"
            item-text="name"
            chips
            flat
            solo
          ></v-select>
          <v-text-field
            v-model="car.brand"
            placeholder="Nom de la marque"
          ></v-text-field>
        </v-expansion-panel-content>
      </v-expansion-panel>
      <v-expansion-panel v-if="car.brand">
        <v-expansion-panel-header>
          <template v-slot:default="{ open }">
            <v-row no-gutters>
              <v-col cols="4">Model</v-col>
              <v-col cols="8" class="text--secondary">
                <v-fade-transition leave-absolute>
                  <span v-if="open" key="0">
                    Renseignez le model
                  </span>
                  <span v-else key="1">
                    {{ car.model }}
                  </span>
                </v-fade-transition>
              </v-col>
            </v-row>
          </template>
        </v-expansion-panel-header>
        <v-expansion-panel-content>
          <v-select
            v-model="car.model"
            :items="models"
            item-text="name"
            chips
            flat
            solo
          >
          </v-select>
          <v-text-field
            v-model="car.model"
            placeholder="Nom du model"
          ></v-text-field>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
  </div>
  <div v-else>
    <v-snackbar v-model="snackbar" :color="color" :top="top" :timeout="timeout">
      Identifiants incorrect !
      <v-btn color="black" text @click="snackbar = false">
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </v-snackbar>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  props: ["brand", "model", "generation", "trim", "characteristic"],
  data() {
    return {
      brands: [],
      models: [],
      generations: [],
      trims: [],
      characteristics: [],
      valid: true,
      car: {
        brand: null,
        model: null,
        generation: null,
        trim: null,
        characteristic: null
      }
    };
  },
  computed: {
    ...mapGetters({
      user: "user"
    })
  },
  methods: {
    checkIdBrand() {
      if (this.model.id_brand === this.brand.id_brand) {
        return true;
      }
    }
  },
  created() {
    this.$axios.get("brand.json").then(response => {
      this.brands = response.data;
    }),
      this.$axios.get("model.json").then(response => {
        this.models = response.data;
      }),
      this.$axios.get("generation.json").then(response => {
        this.generations = response.data;
      }),
      this.$axios.get("trim.json").then(response => {
        this.trims = response.data;
      }),
      this.$axios.get("characteristic.json").then(response => {
        this.characteristics = response.data;
      });
  }
};
</script>
