<template>
  <v-container id="listModels">
    <v-row dense v-for="model in models" :key="model.id" :model="model">
      <v-col v-if="model.id_brand == BrandURLId" cols="12">
        <v-card
          :to="`/cars/${BrandURLId}/${model.id_model}`"
          color="#1F7087"
          dark
        >
          <div class="d-flex flex-no-wrap justify-space-between">
            <div>
              <v-card-title class="headline" v-text="model.name"></v-card-title>
            </div>
          </div>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  props: ["model", "brand"],
  data() {
    return {
      models: [],
      brands: [],
      BrandURLId: this.$route.params.idBrand
    };
  },
  created() {
    this.$axios.get("model.json").then(response => {
      this.models = response.data;
    });
  }
};
</script>
