<template>
  <v-container id="listTrims">
    <v-row dense v-for="trim in trims" :key="trim.id" :trim="trim" cols="12">
      <v-col v-if="trim.id_generation == GenerationURLId" cols="12">
        <v-card
          color="orange"
          :to="
            `/cars/${BrandURLId}/${ModelURLId}/${GenerationURLId}/${trim.id_trim}`
          "
          dark
        >
          <div class="d-flex flex-no-wrap justify-space-between">
            <div>
              <v-card-title
                class="headline"
                v-text="trim.kwh_power + ' ' + trim.unit_kwh"
              ></v-card-title>

              <v-card-subtitle
                v-text="
                  trim.ch_power +
                    ' ' +
                    trim.ch_unit +
                    ' | ' +
                    Math.round(((trim.kwh_power * 0.736) / 40) * 1.6) +
                    ' CV'
                "
              ></v-card-subtitle>
            </div>
          </div>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  props: ["trim"],
  data() {
    return {
      trims: [],
      BrandURLId: this.$route.params.idBrand,
      ModelURLId: this.$route.params.idModel,
      GenerationURLId: this.$route.params.idGeneration
    };
  },
  methods: {
    cv() {
      (this.trim.power / 40) * 1.6;
    }
  },
  created() {
    this.$axios.get("trim.json").then(response => {
      this.trims = response.data;
    });
  }
};
</script>
