<template>
  <v-container>
    <main>
      <v-row>
        <v-col :span="12">
          <div class="preview-vue-rss-feed">
            <NewsHome :feedUrl="feedUrl" :name="name" :limit="limit" />
          </div>
        </v-col>
      </v-row>
    </main>
  </v-container>
</template>

<script>
import NewsHome from "@/components/NewsHome.vue";
const PIXALS_FROM_BOTTOM = 200;
export default {
  name: "Demo",
  components: {
    NewsHome
  },
  data() {
    return {
      feedUrl: "https://rss.app/feeds/uxOLquyyHNv2IM9Y.xml",
      name: "Actualités récentes",
      limit: 5
    };
  },
  mounted() {
    this.scrollEvent = window.addEventListener("scroll", () => {
      if (
        window.innerHeight + window.scrollY >=
        document.body.offsetHeight - PIXALS_FROM_BOTTOM
      ) {
        this.increaseLimit();
      }
    });
  },
  destroyed() {
    if (this.scrollEvent) {
      window.removeEventListener(this.scrollEvent);
    }
  },
  methods: {
    submitForm() {
      if (this.rssFeedForm.feedUrl) {
        this.feedUrl = this.rssFeedForm.feedUrl;
        // Reset limit
        this.limit = 5;
      }
    },
    increaseLimit(loadMore = 5) {
      this.limit += loadMore;
    },
    loadExample(evt) {
      evt.preventDefault();
      this.rssFeedForm.feedUrl = evt.toElement.href;
      this.feedUrl = evt.toElement.href;
    }
  },
  watch: {
    getFeedUrl() {
      return this.feedUrl;
    }
  }
};
</script>
