import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";

Vue.use(Vuex);

const brand = {
  namespaced: true,
  state: {
    datas: []
  },
  mutations: {
    addMany(state, brands) {
      state.datas = brands;
    }
  },
  actions: {
    fetchDatas(context) {
      axios.get("brand.json").then(res => {
        const data = res.data;
        context.commit(
          "addMany",
          Object.keys(data).map(key => data[key])
        );
      });
    }
  }
};

const car = {
  namespaced: true,
  state: {
    datas: []
  },
  mutations: {
    addOne(state, car) {
      state.datas.push(car);
    },
    addMany(state, cars) {
      state.datas = cars;
    }
  },
  actions: {
    saveOne(context, car) {
      axios.post("cars.json", car).then(() => {
        context.commit("addOne", car);
      });
    }
  }
};

const store = new Vuex.Store({
  modules: {
    car,
    brand
  },
  state: {
    user: {
      loggedIn: false,
      data: null
    }
  },
  getters: {
    user(state) {
      return state.user;
    }
  },
  mutations: {
    SET_LOGGED_IN(state, value) {
      state.user.loggedIn = value;
    },
    SET_USER(state, data) {
      state.user.data = data;
    }
  },
  actions: {
    fetchUser({ commit }, user) {
      commit("SET_LOGGED_IN", user !== null);
      if (user) {
        commit("SET_USER", {
          displayName: user.displayName,
          email: user.email
        });
      } else {
        commit("SET_USER", null);
      }
    }
  }
});

export default store;
