<template>
  <v-container class="grey lighten-5">
    <v-row>
      <v-col
        v-for="brand in brands"
        :key="brand.id"
        :brand="brand"
        cols="6"
        xs="1"
        sm="4"
        md="4"
        lg="2"
        xl="2"
        ma-5
      >
        <v-card :to="`/cars/${brand.id_brand}/`">
          <v-img
            :src="brand.image"
            class="white--text align-end"
            gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)"
            height="100px"
          >
          </v-img>
          <v-card-title v-text="brand.name"></v-card-title>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
// @ is an alias to /src
import { mapState } from "vuex";

export default {
  name: "Cars",
  computed: {
    ...mapState("brand", {
      brands: "datas"
    })
  },
  created() {
    this.$store.dispatch("brand/fetchDatas");
  }
};
</script>
