<template>
  <v-container id="listGeneration">
    <v-row
      dense
      v-for="generation in generations"
      :key="generation.id"
      :generation="generation"
      cols="12"
    >
      <v-col v-if="generation.id_model == ModelURLId" cols="12">
        <v-card
          color="red"
          :to="`/cars/${BrandURLId}/${ModelURLId}/${generation.id_generation}`"
          dark
        >
          <div class="d-flex flex-no-wrap justify-space-between">
            <div>
              <v-card-title
                class="headline"
                v-text="generation.name"
              ></v-card-title>

              <v-card-subtitle
                v-text="
                  'Année : ' +
                    generation.year_begin +
                    ' - ' +
                    generation.year_end
                "
              ></v-card-subtitle>
            </div>
          </div>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  props: ["generation"],
  data() {
    return {
      generations: [],
      BrandURLId: this.$route.params.idBrand,
      ModelURLId: this.$route.params.idModel
    };
  },
  created() {
    this.$axios.get("generation.json").then(response => {
      this.generations = response.data;
    });
  }
};
</script>
